* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

/* ------------------------------- NAVBAR ------------------------------- */

.navbar {
  font-family: "Roboto Slab", serif;
  font-size: 18px;
  background-color: black;
  border: none;
  padding-bottom: 10px;
  width: 100%;
  position: fixed;
  z-index: 5;
}

.main-nav {
  list-style-type: none;
  display: none;
}

.nav-links,
.logo {
  text-decoration: none;
  color: rgba(255, 255, 255, 1);
}

.main-nav li {
  text-align: center;
  margin: 15px auto;
}

.logo {
  display: inline-block;
  font-size: 22px;
  margin-top: 10px;
  margin-left: 20px;
}

.navbar-toggle {
  position: absolute;
  top: 10px;
  right: 20px;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.8);
  font-size: 24px;
}

.active {
  display: block;
}

@media screen and (min-width: 768px) {
  .navbar {
    display: flex;
    justify-content: space-between;
    padding-bottom: 0;
    height: 50px;
    align-items: center;
  }

  .main-nav {
    display: flex;
    margin-right: 30px;
    flex-direction: row;
    justify-content: flex-end;
  }

  .main-nav li {
    margin: 0;
  }

  .nav-links {
    margin: 0 15px 0 15px;
  }

  .logo {
    margin-top: 0;
  }

  .navbar-toggle {
    display: none;
  }

  .logo:hover,
  .nav-links:hover {
    color: rgba(255, 255, 255, 0.7);
  }
}

/* ------------------------------- MISC ------------------------------- */

.wrapper {
  width: 100%;
  margin: 0 50px 0 0;
  padding: 0;
}

.container {
  width: 100%;
  padding: 50px 0;
}

/* ------------------------------- ANIMATIONS ------------------------------- */

@keyframes fadeIn {
  0% {
    transform: translateY(-10%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes wiggle {
  0% {
    transform: rotate(5deg);
  }
  25% {
    transform: rotate(-5deg);
  }
  50% {
    transform: rotate(10deg);
  }
  75% {
    transform: rotate(-2.5deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes widen {
  0% {
    transform: scale3d(1, 1, 1);
    opacity: 0.2;
  }
  50% {
    transform: scale3d(1.1, 1.1, 1.1);
    opacity: 1;
  }
  100% {
    transform: scale3d(1, 1, 1);
    opacity: 0.2;
  }
}

.fall {
  animation: fadeIn 2s;
}

.widen:hover {
  animation: widen 1.3s ease-in-out infinite;
}

.grow {
  transition: all 0.2s ease-in-out;
}

.grow:hover {
  transform: scale(1.1);
}

/* ------------------------------- BACKGROUND CONTROL ------------------------------- */

#intro {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("./assets/self-min.jpg") no-repeat center center fixed;
  background-size: cover;
  padding: 20% 0 20% 0;
  text-align: center;
  color: white;
  height: auto;
}

#about {
  background: white;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5% 0 10% 0;
  height: auto;
}

#skills {
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url("./assets/sf-min.jpg") no-repeat center center fixed;
  background-size: cover;
  height: auto;
}

#projects {
  background: #542437;
  height: auto;
}

#resume {
  background: linear-gradient(rgba(32, 11, 214, 0.7), rgba(89, 34, 161, 0.2))
    no-repeat center center fixed;
  height: 300px;
}

#contact {
  background: #222;
  height: auto;
}

@media screen and (max-width: 768px) {
  #intro {
    background: linear-gradient(
        rgba(26, 139, 204, 0.7),
        rgba(26, 139, 204, 0.2)
      )
      fixed;
    padding: 20% 0 20% 0;
  }

  #skills {
    background: linear-gradient(rgba(187, 58, 58, 0.7), rgba(233, 32, 32, 0.2))
      fixed;
  }
}


/* ------------------------------- PROJECTS ------------------------------- */

.projectsContainer {
  margin: auto;
  width: 95%;
  border: 1px solid white;
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.project {
  height: 200px;
  border: 1px dotted white;
  margin: 10px;
  width: 33%
}

@media screen and (max-width: 1200px) {
  .projectsContainer {
    flex-direction: column;
  }
  
  .project {
    width: 95%;
  }
}